"use client";
import React, { useState } from "react";
import { supabase } from "../../database/supabaseClient";

/**
 * USERS FOR TEST
 */
const USERS = [
  {
    label: "member_user1",
    email: "user1@test.com",
    password: "password1",
  },
  {
    label: "authenticator",
    email: "authenticator@songvietnam.event",
    password: "3CN29r6NWFczcXk",
  },
] as const;

export default function CustomRolesTest() {
  const [log, setLog] = useState<string[]>([]);
  const append = (msg: string) =>
    setLog((prev) => [...prev, `[${new Date().toLocaleTimeString()}] ${msg}`]);

  const runTest = async (user: (typeof USERS)[number] | null) => {
    const actor = user ? user.label : "public";
    append(`\n=== Testing as ${actor} ===`);

    try {
      let currentUid: string | null = null;
      const TARGET_EVENT_ID = "song-viet-nam";

      // üîë login or anon
      if (user) {
        const { error: loginErr } = await supabase.auth.signInWithPassword({
          email: user.email,
          password: user.password,
        });
        if (loginErr) {
          append(`‚ùå Login failed for ${user.email}: ${loginErr.message}`);
          return;
        }

        const {
          data: { user: loggedUser },
        } = await supabase.auth.getUser();
        currentUid = loggedUser?.id ?? null;

        append(
          `‚úÖ Logged in as ${user.email} (uid: ${currentUid ?? "unknown"})`
        );
      } else {
        await supabase.auth.signOut();
        append("üë§ Using public (anon) session");
      }

      // ===== 1Ô∏è‚É£ READ TEST =====
      append(`üîç Attempting SELECT * FROM events`);
      const { data: events, error: readErr } = await supabase
        .from("events")
        .select("*");

      if (readErr) {
        append(`üö´ Read error: ${readErr.message}`);
      } else {
        append(
          `üìñ Read success: ${events?.length ?? 0} rows returned` +
            (events && events.length > 0
              ? `\n   ‚Üí First row sample:\n${JSON.stringify(
                  events[0],
                  null,
                  2
                )}`
              : "")
        );
      }

      // ===== 2Ô∏è‚É£ WRITE TEST =====
      const randomEventName = "Test Event " + Math.floor(Math.random() * 1000);
      const newEvent = {
        event: randomEventName,
        event_id: TARGET_EVENT_ID,
        paid: true,
        used: null,
        user_id: currentUid,
      };

      append(
        `üìù Attempting INSERT into events with data:\n${JSON.stringify(
          newEvent,
          null,
          2
        )}`
      );

      const { data: insertData, error: insertErr } = await supabase
        .from("events")
        .insert([newEvent])
        .select();

      if (insertErr) {
        if (insertErr.code === "42501") {
          append("üö´ Insert blocked by RLS policy (permission denied)");
        } else {
          append(`‚ùå Insert error: ${insertErr.message}`);
        }
      } else if (insertData && insertData.length > 0) {
        append(
          `‚úÖ Insert success: inserted event_id=${insertData[0].event_id}`
        );
      } else {
        append("‚ÑπÔ∏è Insert query ran but returned no data (check policies)");
      }

      // ===== 3Ô∏è‚É£ UPDATE TEST =====
      append(`üîß Attempting UPDATE on events with filters:
   user_id = ${currentUid}
   event_id = ${TARGET_EVENT_ID}`);

      const updatePayload = { used: new Date().toISOString() };

      const { data: updatedRows, error: updateErr, status } = await supabase
        .from("events")
        .update(updatePayload)
        .eq("user_id", currentUid)
        .eq("event_id", TARGET_EVENT_ID)
        .select();

      const filterInfo = `(event_id: ${TARGET_EVENT_ID}, user_id: ${currentUid})`;

      if (updateErr) {
        if (updateErr.code === "42501") {
          append(`üö´ Update blocked by RLS policy ${filterInfo}`);
        } else {
          append(`‚ùå Update failed: ${updateErr.message} ${filterInfo}`);
        }
      } else if (updatedRows && updatedRows.length > 0) {
        append(
          `‚úÖ Update success\n   ‚Üí Updated rows:\n${JSON.stringify(
            updatedRows,
            null,
            2
          )}`
        );
      } else {
        append(`‚ÑπÔ∏è Update ran successfully, but no rows matched ${filterInfo}`);
      }

      await supabase.auth.signOut();
      append(`--- End test for ${actor} --- ‚úÖ`);
    } catch (err: any) {
      append(`‚ùå Unexpected error for ${actor}: ${err.message}`);
      console.error(err);
      await supabase.auth.signOut();
    }
  };

  return (
    <div
      style={{
        fontFamily: "monospace",
        padding: 20,
        maxWidth: 900,
        margin: "auto",
        color: "white",
      }}
    >
      <h2>üîí Access Test ‚Äì Events Table (Read/Write Validation)</h2>
      <div style={{ display: "flex", gap: 8, flexWrap: "wrap" }}>
        {USERS.map((u) => (
          <button
            key={u.email}
            onClick={() => runTest(u)}
            style={{ padding: "8px 12px" }}
          >
            Test as {u.label}
          </button>
        ))}
        <button onClick={() => runTest(null)} style={{ padding: "8px 12px" }}>
          Test as Public
        </button>
      </div>

      <pre
        style={{
          background: "#000",
          color: "#0f0",
          padding: 15,
          borderRadius: 8,
          marginTop: 20,
          height: 500,
          fontSize: 12,
          overflowY: "auto",
          whiteSpace: "pre-wrap",
        }}
      >
        {log.join("\n")}
      </pre>
    </div>
  );
}
